import React from 'react'
import "./About.css"
import Me from "../../image/me9.webp"
import Award from "../../image/awaed4.svg"


function About() {
  
    return (
        <div className="a">
           <div className="a-left">
               <div className="a-card bg"></div>
               <div className="a-card">
                   <img src={Me} alt="imagee" className="a-img"/>
               </div>
           </div>
           <div className="a-right">
               <h1 className="a-title" >About me</h1>
               <p className="a-sub"> Hello everyone , I am an 19 year (currently) old boy . I am from Navsari , Gujarat . I don't 
               have much to tell about myself . Still , 
                   </p>
                   <p className="a-desc">After getting a decent rank (which have nothing to do with my coding skills) 
                   I got admission in Charusat University in B. Tech I.T(Information technology) field . And now I am currently 
                   an second year college student having lots of fun and assignment to do .
                    
                    
                </p>
                <div className="a-award">
                 
                        <img  src={Award} alt="a-award" className="a-award-img" />
                        <div className="a-award-texts">
                            <h4 className="a-award-title">International Design</h4>
                            <p className="a-award-desc"> Well ,what should i tell u about my designing skill
                            , it's too good (for me only...) . 
                    </p>
                        </div>
                    
                </div>
           </div>
        </div>
    )
}

export default About
